.page-header__container {
  position: relative;
  height: 56px;
}
.page-header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0px);
  z-index: 50;
  height: 96px;
}
@media (min-width: 1200px) {
  .page-header__logo span {
    background-image: url('../img/fone-logo.svg');
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0px);
    z-index: -1;
    width: 198px;
    height: 96px;
  }
}
.page-header__burger {
  display: block;
  position: relative;
  width: 23px;
  height: 20px;
  z-index: 60;
  cursor: pointer;
  margin-right: 20px;
}
@media (min-width: 768px) {
  .page-header__burger {
    display: none;
  }
}
.page-header__burger span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #81B3D2;
  top: 8px;
  transition: all 0.3s ease 0s;
}
.page-header__burger:before,
.page-header__burger:after {
  content: "";
  background-color: #81B3D2;
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  z-index: 60;
  transition: all 0.3s ease 0s;
}
.page-header__burger:before {
  top: 0;
}
.page-header__burger:after {
  bottom: 0;
}
.page-header__burger.active:before {
  transform: rotate(45deg);
  top: 9px;
}
.page-header__burger.active:after {
  transform: rotate(-45deg);
  bottom: 9px;
}
.page-header__burger.active span {
  transform: scale(0);
}
@media (max-width: 767px) {
  .page-header__container {
    height: 100%;
  }
  .page-header__burger-wrap {
    height: 56px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .page-header__logo {
    top: inherit;
  }
}
