.causes-gray {
  background-color: @backgound-gray;
  @media (min-width: @tablet) {
    min-height: 256px;
  }
  @media (max-width: @mobile) {
    min-height: 503px;
   }
}
.causes-gray__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-top: 44px;
  @media (max-width: @mobile) {
   flex-direction: column;
   padding-top: 27px;
  }
}
.causes-gray__list-item {
  list-style: none;
  // width: 33%;
  width: 350px;
  margin-bottom: auto;
  @media (max-width: @desktop) {
    width: 240px;
  }
  @media (max-width: @mobile) {
    // width: 99%;
    margin-bottom: 28px;
    border-bottom: 1px solid @line-gray;
    width: 280px;
  }
}
.causes-gray__list-item:last-of-type {
  @media (max-width: @mobile) {
    margin-bottom: 0;
    border-bottom: none;
  }
}

.causes-gray__title {
  .pt-sans__bold();
  font-size: 21px;
  line-height: 26px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 12px;
  @media (max-width: @mobile) {
    font-size: 18px;
  }
}
.causes-gray__title br{
  @media (max-width: @mobile) {
    display: none;
  }
}
.causes-gray__description {
  .pt-sans__normal();
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  color: @basic-description;
  margin: 0;
  margin-bottom: 22px;
  padding: 0 30px;
  @media (max-width: @mobile) {
    margin-bottom: 15px;
    padding: 0 15px;
  }
}
.causes-gray__description br{
  @media (max-width: @mobile) {
    display: none;
  }
}
.causes-gray__description--line {
  @media (max-width: @mobile) {
    margin-bottom: 23px;
  }
}
